.navbar_main {
    width: 90%;
    max-width: 1366px;
    margin: 2em auto;
    font-size: 1rem;
}

.nav_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav_items {
    display: flex;
    justify-content: center;
}

#brand_logo {
    height: 30px;
}

.nav_items a {
    padding: 0 1em;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 500;
    color:#CDCDCD;
}

.nav_items a:hover {
    color: white;
}

.nav_items .current_page {
    color:#707070 ;
}

.main_btn {
    text-decoration: none;
    padding: .6rem 1.2rem;
    border: 1px solid #25d365;
    display: flex;

    color: rgb(255, 255, 255);
    border-radius: 30px;
    font-size: 16px;
    font-weight: 400;
    cursor: pointer;
}

.whatsapp_icon {
    margin-right: .2rem;
}

.main_btn:hover {
    background: #25d365;
}

/*--------------------------- Responsive Starts Drom Here --------------------------------------------*/
.lab_navbar_menu {
    display: none;
    position: relative;
}

.lab_navbar_menu img {
    cursor: pointer;
}

.drag1 {
    height: 4px;
    width: 30%;
    background: linear-gradient(300deg, #e10b7a 0%, #652D90 49.48%, #1C85C7 100%);
    position: absolute;
    top: 1rem;
    border-radius: 1rem;
}
.lab_menu_container {
    display: flex;
    justify-content: center;
    align-items: center;

    background: #e2e2e2;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;

    position: fixed;
    width: 100%;
    top: 20rem;
    left: 0;
    bottom: 0;
    z-index: 99;
}

.lab_menu_links {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.lab_menu_links hr {
    border-radius: 2px;
}

.lab_menu_container a {
    text-decoration: none;
    color: #2e2e2e;
    font-size: 1rem;
    font-weight: 600;
    line-height: 2.8rem;
}

.lab_menu_container a:hover {
    font-weight: 700;
    color: #000;
}

.manu_btn {
    display: none;
}

@media screen and (max-width: 960px) {
    .main_btn {
        display: none;
    }
        .nav_items {
        display: none;
    }
    .lab_navbar_menu {
        display: flex;
        justify-content: space-between;
    }
}

@media screen and (max-width: 800px) {

    .main_btn {
        display: none;
    }
    .lab_navbar_menu {
        display: flex;
        justify-content: space-between;
    }
}

@media screen and (max-width: 700px) {
    .navbar_main {
        width: 90%;
    }
}

@media screen and (max-width: 640px) {
    .lab_navbar_menu {
        padding: 0 0 ;
    }

    .contact-us_btn {
        display: none;
    }
}

