.main_client {
    width: 90%;
    max-width: 1366px;
    margin: 0 auto;
}

.clients_title {
    margin-bottom: 4rem;
    text-align: center;
    font-size: 44px;
    font-weight: bold;
    opacity: .2;
    text-transform: uppercase;
}

.clients_title:hover {
    opacity: .7;
}

.clients_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    border-radius: 26px;
    background: #16161670;
}

.ratings {
    display: flex;
    gap: 20px;
    padding: 10px 0;
}

.google_review_images {
    background-size: cover;
    object-fit: cover;
    border-radius: 50%;
}

.goto_links {
    display: flex;
    gap: 20px;
}

.goto_links a {
    text-decoration: none;
    font-style: italic;
    background-image: var(--mainBg);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 600;
}

.goto_links a:hover {
    font-style:normal; 
}

.star_icon {
    color: yellow;
}

.clients_comm_area {
    padding: 50px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 45px;
}

.comm_box {
    width: 256px;
    height: 100%;
    background: #16161670;
    padding: 20px;
    border-radius: 10px;
}

.comm_box:hover {
    background: #161616bb;
}

.box_head {
    display: flex;
    align-items: center;
    gap: 10px;
}

.clients_say {
    padding-top: 20px;
    opacity: .7;
    font-weight: 400;
}

.clients_say:hover {
    opacity: 1;
}

@media (min-width: 1249px) {
    .clients_comm_area {
        gap: 45px;

    }
    .comm_box {
    width: 22%;
    }
}

@media (max-width: 1248px) {
    .clients_comm_area {
        gap: 45px;

    }
    .comm_box {
    width: 30%;
    }
}

@media (max-width: 999px) {
    .clients_comm_area {
        gap: 35px;

    }
    .comm_box {
    width: 48%;
    }
}

@media (max-width: 972px) {
    .clients_comm_area {
        gap: 45px;

    }
    .comm_box {
    width: 47%;
    }
}


@media (max-width: 832px) {
    
    .clients_comm_area {
        gap: 45px;

    }
    .comm_box {
    width: 46%;
    }
}

@media screen and (max-width: 640px) {
    .clients_comm_area {
        gap: 20px;
    }
  .comm_box {
    width: 100%;
  }
}

@media screen and (max-width: 460px) {
    .clients_title {
        margin-top: 2rem;
        font-size: 1.5rem;
    }

    .clients_header {
        border-radius: .7rem;
    }

    .goto_links .links {
        font-weight: 400;
        font-size: 14px;
    }

    .clients_comm_area {
        gap: .5rem;
        padding: 15px 0;
    }
}