.main_hero {
    width: 90%;
    max-width: 1366px;
    height: 100vh;
    margin: 0 auto;

}

.hero_content_holder_container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content {
    text-align: center;
    font-size: 64px;
    opacity: .2;
    line-height: 65px;
}

.content:hover {
    transition: ease-in ease-out 1.5s;
    opacity: .7;
}

.hero_btn_holder {
    display: flex;
    justify-content: center;
}

.hero_btn {
    margin: 4em auto;
    padding: 15px 25px;
    color: white;
    border-radius: 30px;
    background: linear-gradient(black, black) padding-box, border-box var(--mainBg);
    border: 2px solid transparent;
    cursor: pointer;
    font-size: 1em;
}


.hero_btn:hover {
    box-shadow: 1px 1px 5px var(--thirdColor);
}

.scroll_animation {
    display: flex;
    flex-direction: column;
}

.scroll_animation p {
    text-align: center;
    margin-top: .5rem;
    opacity: .2;
}

.scroll_down {
    height: .7rem;
    width: .7rem;
    margin: 0 auto;
    background: var(--mainBg);
    border-radius: 50%;
}

.scroll_down {
  animation: scroll_down 2s ease infinite;
}
@keyframes scroll_down {
  0% { transform: translateY(0); }
  20% {
    opacity: 1;
    transform: translateY(-20px);
  }
  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

