@media screen and (max-width: 1080px) {
    .content {
        font-size: 3rem;
    }
}

@media screen and (max-width: 800px) {
    .content {
    font-size: 34px;
    margin-top: 100px;
    }
}

@media screen and (max-width: 700px) {
    .content {
    font-size: 34px;
    margin-top: 30px;
    line-height: 40px;
    display: flex;
    align-items: center;
    }
}

@media screen and (min-width: 700px) {
    .golden_ratio {
    display: none;
    }

}

@media screen and (max-width: 640px) {
    .golden_ratio {
    display: block;
    }

    .content {
        margin-top: 20px;
    }
}

@media screen and (max-width: 480px) {
    .main_hero {
        height: 80vh;
    }

    .hero_content_holder_container {
        height: 80vh;
    }
    
    .golden_ratio {
        margin-top: 1rem;
        height: 250px;
        width: 250px;
        background-size: cover;
        object-fit: cover;
        opacity: .7;
    }

    .content {
        font-size: 1.5rem;
        opacity: 1;
    }
}