.your_carousel_box {
    width: 100%;
    padding: 20px;
    
}


.y_customer_img {
    border-radius: 30px;
    opacity: .2;
    object-fit: cover;
    width: 100%;
    height: 200px;
}

.your_heading {
    opacity: .2;
    padding: 10px 0;
}

.y_customer_img:hover, 
.your_heading:hover, 
.your_para:hover {
    opacity: .7;
}

.your_para {
    font-size: 1.2rem;
    opacity: .2;
    padding: 20px 0;
}



.your_customer_btn {
    border: 1px solid white;
    padding: 10px 20px;
    color: white;
    border-radius: 30px;
    background: linear-gradient(black, black) padding-box, border-box var(--mainBg);
    border: 2px solid transparent;
    cursor: pointer; 
    font-size: 1em;
}

.your_customer_btn:hover {
    box-shadow: 1px 1px 5px var(--thirdColor);
}

@media (max-width: 620px) {
    .y_customer_img {
        height: 300px;
    }
}

@media screen and (max-width: 960px) {
    .y_customer_img, 
    .your_heading, 
    .your_para {
        opacity: 1;
    }
}

@media screen and (max-width: 480px) { 
    .main_your_title {
        font-size: 1.5rem;
        margin-bottom: 2rem;
    }

    .your_carousel_box {
        width: 95% !important;
        padding: .5rem;
    }

    .y_customer_img {
        opacity: 1;
        height: 200px;
    }
    .your_heading {
        font-size: 1.2rem;
        margin-top: 1.2rem;
        opacity: 1;
    }

    .your_para {
        font-size: .8rem;
        line-height: 1rem;
        padding-top: 15px 0;
        opacity: 1;
    }

    .your_customer_btn {
        padding: 8px 15px;
        font-size: .8rem;
    }
}