
.slick-slider {
    width: 100%!important;
    padding: 0 20px !important;
}

.pkg_box {
    background: #ffffff0c;
    border-radius: 1rem;
    position: relative;
    height: 100% !important;
}

.pkg_box_hover:hover {
    background: #ffffff13;
}

.box2, .box4 {
    background: linear-gradient(45deg, #E10B7B 0%, #652D90 49.48%, #1C85C7 100%);
}

ul {
    list-style: none;
    padding-top: 2rem;
    margin: 1rem;
    padding-bottom: 4rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4rem;
}

.package_list_flex {
    display: flex;
    justify-content: flex-start;
    gap: 5px;
}

.pkg_inside_title {
    text-align: center;
    margin: 10px 0;
}

.button_flex {
    display: flex;
    justify-content: center;
}

.logolab_pricing_btn {
    width: 70%;
    background: #fff;
    margin: auto;
    border: none;
    outline: none;
    padding: .6em 2em;
    
    font-size: 1rem;
    font-weight: 700;
    border-radius: 2rem;
}

.pkg_btn_holder {
    display: flex;
    justify-content: center;
}

.pkg_btn {
    position: absolute;
    bottom: .5rem;
    padding: 10px 25px;
    color: white;
    border-radius: 30px;    
    background: linear-gradient(black, black) padding-box, border-box var(--mainBg);
    border: 2px solid transparent;
    cursor: pointer;
    font-size: 1em;
}

.pkg_btn:hover {
    box-shadow: 1px 1px 5px var(--thirdColor);
}

@media (min-width: 1080px) {
    .pkg_box {
        width: 100%!important;
        height: 110vh!important;
        margin: 0 auto !important;
    }
}

@media (max-width: 1080px) {
    .pkg_box {
        width: 100%!important;
        height: 80vh!important;
        margin: 0 auto !important;
    }
}

@media (max-width: 960px) {
    .pkg_box {
        width: 100%!important;
        height: 80vh!important;
        margin: 0 auto !important;
    }
}

@media (max-width: 800px) {
    .pkg_box {
        width: 90%!important;
        height: 80vh!important;
        margin: 0 auto !important;
    }
}

@media (max-width: 640px) {
    .pkg_box {
        width: 90%!important;
        height: 70vh!important;
        margin: 0 auto !important;
    }
}

@media screen and (max-width: 460px) {
    .pkg_box {
        width: 90%!important;
        height: 70vh!important;
        margin: 0 auto !important;
        padding: 0;
    }

    .package_list_flex {
        font-size: 14px;
    }

    .pkg_btn {
        padding: 8px 20px;
        font-size: .8rem;
    }
}
