.main_works {
    width: 90%;
    max-width: 1366px;
    padding: 2rem 0;
    margin: 0 auto;
}

.works_title {
    font-size: 44px;
    font-weight: bold;
    opacity: .2;
    text-align: center;
    text-transform: uppercase;
}

.works_title:hover {
    transition: ease-in ease-out 1.5s;
    opacity: .7;
}

.card_container {
    display: flex;
    flex-wrap: wrap;
    padding: 3rem 0;
    width: 100%;
}

.cards {
    -ms-flex: 33.33%; /* IE10 */
  flex: 33.33%;
  max-width: 33.33%;
}

.images {
    width: 100%;
    opacity: .2;
}

.images:hover {
    opacity: .7;
}

@media screen and (max-width: 960px) {
    .images {
        opacity: 1;
    }
}

@media screen and (max-width: 800px) {
  .cards {
    -ms-flex: 50%;
    flex: 50%;
    max-width: 50%;
  }
}

@media (min-width: 640px) {
    .responsive-slider {
        display: none;
    }
}

@media (max-width: 640px) {
    .card_container {
        display: none;
    }
    .responsive-slider {
        width: 100%;
        display: block;
        margin: 1.5rem auto;
    }
    .cards {
        display: flex!important;
    }

    .images {
        object-fit: cover;
        border-radius: 1rem;
        width: 300px;
    }
}

 @media screen and (max-width: 480px) {

    .works_title {
        font-size: 1.5rem;
        margin-top: 4em;
    }
    .images {
        width: 180px;
        opacity: 1;
    }
    
 }
