.main_case {
    width: 90%;
    max-width: 1366px;
    margin: 4rem auto;
}

.main_case_title {
    margin-bottom: 4rem;
    display: flex;
    justify-content: center;
    font-size: 44px;
    font-weight: bold;
    opacity: .2;
    text-transform: uppercase;
}

.main_case_title:hover {
    opacity: .7;
}

@media screen and (max-width: 480px) { 
    .main_case_title {
        font-size: 1.5rem;
        margin-bottom: 2rem;
    }

    .main_case {
        width: 100%;
    }
}