.main_logo_process {
    width: 90%;
    max-width: 1366px;
    margin: 0 auto;
}

.logo_process_title {
    font-size: 44px;
    font-weight: bold;
    opacity: .2;
    text-align: center;
    text-transform: uppercase;
    margin-top: 50px;
}

.logo_process_title:hover {
    transition: ease-in ease-out 1.5s;
    opacity: .7;
}

.lr_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 100px;
    margin: 50px 0;
}

.left_process {
    width: 50%;
    background: #ffffff09;
    display: flex;
    flex-direction: column;
    padding: 1.5rem 2rem;
    border-radius: 2rem;
}

.right_process {
    width: 50%;
}

.logo_process_video {
    width: 100%;
    opacity: .1;
    border-radius: 30px;
    transition: opacity 1s ease-in ;
}

.logo_process_video:hover {
    opacity: 1;
    
}

.process_title {
    opacity: .4;
}

.logo_process_points {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
}

.logo_process_points h2 {
    opacity: .2;
}

.logo_process_points hr {
   opacity: .2;
   margin: 10px 0;
   border-radius: 2px;
}

.logo_process_points h2:hover {
    opacity: 1;
}

@media (max-width: 960px) {
    
    .logo_process_video {
        opacity: 1;
    }
    .logo_process_points h2 {
        opacity: 1;
    }
}

@media (max-width: 800px) {
    .lr_container {
        flex-direction: column;
    }
    .left_process,.right_process {
        width: 90%;
        align-items: center;
    }
}

@media (max-width: 600px) {
    .lr_container {
        gap: 0rem;
    }
}


 @media screen and (max-width: 480px) {

    .lr_container {
        margin-bottom: 0;
    }

    .logo_process_title {
        font-size: 1.5rem;
    }

    .process_title{
        font-size: 1.3rem;
    }

    .logo_process_points {
        text-align: flex-start
    }

    .logo_process_points h2 {
        width: 100%;
        font-size: 1rem;
        opacity: .9;
        margin: .5rem 0;
    }

    .logo_process_video {
        margin-top: -3.5rem;
        opacity: 1;
    }

    .logo_process_video_quote {
        display: none;
    }

    
 }