.case_box {
    width: 100%;
    height: 80vh !important;
    padding: 0 20px;
    
}

.case_study_image {
    /* background: url("../../../public/b&a.jpg"); */
    border-radius: 20px;
    object-fit: cover;
    opacity: .2;
    width: 100%;
    height: 200px;

}

.case_study_image:hover {
    opacity: 1;
}

.before_n_after {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
}

.bNa_title {
    width: 50%;
    display: flex;
    justify-content: center;
}

.logo_box {
    height: 250px;
    display: flex;
    padding: 30px;
    justify-content: space-between;
    align-items: center;
}

.before {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    border-radius: 15px;
    width: 220px;
    padding: 20px;
    background: #000;
}

.btwn_line {
    height: 90%;
    width: 1px;
    background: white;
    opacity: .2;
    border-radius: 2px;
}

.after {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    border-radius: 15px;
    width: 220px;
    padding: 20px;
    background: #000;
}

.case_content_area {
    margin-top: 10px;
}

.case_box_heading {
    opacity: .2;
    padding: 10px 0;

}

.case_box_para:hover, .case_box_heading:hover {
    opacity: .7;
}

.case_box_para {
    font-size: 1.2rem;
    opacity: .2;
    padding: 20px 0;
}


.case_btn {
    padding: 10px 20px;
    color: white;
    border-radius: 30px;
    background: linear-gradient(black, black) padding-box, border-box var(--mainBg);
    border: 2px solid transparent;
    cursor: pointer;  
    font-size: 1em;
}

.case_btn:hover {
    box-shadow: 1px 1px 5px var(--thirdColor);
}


.model_wrapper {
  position: fixed;
  top: 20%;
  left: 0;
  right: 0;
  bottom: 0;
  background: #ffffff;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  z-index: 999;
}

.more_about_us {
  color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 90%;
  margin: 1.5rem auto;
}

.more_about_us .close_me_btn {
  background: none;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  color: #252525;
  transition: ease-in .1s;
}

.more_about_us .close_me_btn:hover {
  /* transform: rotate(90deg); */
  color: #086894;

}

.about_more_details_pop {
  width: 90%;
  margin: 0 auto;
  overflow-y: scroll;
  height: 100%;
}

.about_popup_paragraph {
  color: #000;
  font-size: 1.2rem;
  font-weight: 400;
  padding: 6rem 1rem 8rem 0;
}


@media screen and (max-width: 960px) {
    .case_study_image, 
    .case_box_heading, 
    .case_box_para {
        opacity: 1;
    }
}

@media (max-width: 620px) {
    .case_study_image {
        height: 300px;
    }
}

@media screen and (max-width: 460px) { 

    .slick-slider {
        padding: 0 !important;
    }
    .case_study_image {
        opacity: 1;
    }

    .case_box {
        width: 95%!important;
        height: 55vh !important;
        padding: 0 .6rem ;
    }

    .case_study_image {
        height: 200px;
    }

    .case_box_heading {
        font-size: 1.2rem;
        margin-top: 1rem;
        opacity: 1;
    }

    .case_box_para {
        font-size: .8rem;
        line-height: 1rem;
        padding: 15px 0;
        opacity: 1;
    }

    .case_btn {
        padding: 8px 15px;
        font-size: .8rem;
    }
}