.main_your_customer {
    width: 90%;
    margin: 0 auto;
    max-width: 1366px;
    padding-bottom: 6rem;
}

.main_your_title {
    margin-bottom: 4rem;
    display: flex;
    justify-content: center;
    font-size: 44px;
    font-weight: bold;
    text-transform: uppercase;
    opacity: .2;
}

.main_your_title:hover {
    opacity: .7;
}

.customer_carousel {
    margin-top: 50px;
}

@media screen and (max-width: 480px) { 
    .main_your_title {
        font-size: 1.5rem;
        margin-bottom: 2rem;
    }

    .main_your_customer {
        width: 100%;
    }
}