.main_logo_pkg {
    width: 90%;
    max-width: 1366px;
    margin: 4rem auto;
    padding: 2rem 0;
}

.logo_pkg_title {
    font-size: 44px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 4rem;
    opacity: .2;
}

.logo_pkg_title:hover {
    opacity: .7;
}

.pkg_boxes {
    width: 100%;
    display: flex !important;
    justify-content: space-between;
    gap: 10px;
    position: relative;

}

.pkg_box {
    background: #ffffff0c;
    border-radius: 1rem;
    position: relative;
    width: 20%;

    /* border: 1px solid rgb(255, 0, 64); */
    /* border-width: 2px; */
    /* border-style: solid; */
    /* border-image: linear-gradient(to left, #E10B7B, #652D90, #1C85C7); */
    /* border-image-slice: 1; */
}

.pkg_box_hover:hover {
    background: #ffffff13;
}

.box1, .box2, .box4 {
    background: linear-gradient(45deg, #E10B7B 0%, #652D90 49.48%, #1C85C7 100%);
}

ul {
    list-style: none;
    padding-top: 2rem;
    margin: 1rem;
    padding-bottom: 4rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4rem;
}

.package_list_flex {
    display: flex;
    justify-content: flex-start;
    gap: 5px;
}

.pkg_inside_title {
    text-align: center;
    margin: 10px 0;
}

.button_flex {
    display: flex;
    justify-content: center;
}

.logolab_pricing_btn {
    width: 70%;
    background: #fff;
    margin: auto;
    border: none;
    outline: none;
    padding: .6em 2em;
    
    font-size: 1rem;
    font-weight: 700;
    border-radius: 2rem;
}

.pkg_btn_holder {
    display: flex;
    justify-content: center;
}

.pkg_btn {
    position: absolute;
    bottom: .5rem;
    padding: 10px 25px;
    color: white;
    border-radius: 30px;    
    background: linear-gradient(black, black) padding-box, border-box var(--mainBg);
    border: 2px solid transparent;
    cursor: pointer;
    font-size: 1em;
}

.pkg_btn:hover {
    box-shadow: 1px 1px 5px var(--thirdColor);
}

.res_caro_pkg_boxes {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (min-width: 1250px) {
    .res_caro_pkg_boxes {
        display: none;
    }
}

@media screen and (max-width: 1249px) {

    .pkg_box {
        display: none;
    }
    .res_caro_pkg_boxes {
        gap: 20px;
    }
}

@media screen and (max-width: 960px) {

    .main_logo_pkg {
        width: 100%;
    }
}

@media screen and (max-width: 480px) {
    
    .main_logo_pkg {
        width: 100%;
        margin: 0;
    }

    .pkg_box {
        height: 90vh !important;
    }

    .logo_pkg_title {
        font-size: 1.5rem;
    }

    .pkg_boxes {
        width: 400px !important;
    }

    
}



